<template>
  <div>
    <el-card shadow='hover' class='card'>
      <div class='type item'>
        <span class='left'>{{$t('Admin.WorkType')}}</span>
        <span class='right'>{{$t('Admin.WorkReport')}}</span>
      </div>
      <div class='from item'>
        <span class='left'>{{$t('Admin.WorkFrom')}}</span>
        <span class='right'> {{userid}}</span>
      </div>

      <div class='target item'>
        <span class='left'>{{$t('Admin.ReportTarget')}} </span>
        <span class='right clickable' @click='showOriginal'>{{targetFormat}}</span>
      </div>
      <div class='reason item'>
        <span class='left'>{{$t('Admin.ReportContent')}}</span>
        <span class='right'>{{content}}</span>
      </div>

      <div class='button'>
        <el-button type='primary' @click='failReport'>{{$t('Admin.ReportFail')}}</el-button>
        <el-button type='danger' @click='successReport'>{{$t('Admin.ReportSuccess')}}</el-button>
      </div>

    </el-card>
  </div>
</template>

<script>
export default {
  name: 'admin-report',
  props: {
    workid: { type: Number },
    type: { type: Number },
    userid: { type: Number },
    target: { type: String },
    targetid: { type: Number },
    content: { type: String },
    original: { type: String },
    time: { type: String },
  },
  computed: {
    targetFormat () {
      let returnText = '';
      switch (this.target) {
        case 'hd':
          returnText = '树洞';
          break;
        case 'hc':
          returnText = '树洞评论';
          break;
        case 'md':
          returnText = '市场';
          break;
        case 'nc':
          returnText = '新闻评论';
          break;
        case 'ld':
          returnText = '失物招领'
          break;
        default:
          returnText = '未知'
      }
      return returnText + ' (点击跳转)'
    }
  },
  data () {
    return {
      userRealName: '',
    }
  },
  methods: {
    showOriginal () {
      console.log(this.targetid);
      if (this.target == 'hc' || this.target == 'nc') {
        localStorage.setItem('adminworkReportShowOriginalCommentID', this.targetid);
      }
      window.open(this.original)
    },
    failReport () {
      this.$bus.$emit('completeAdminWork', this.workid, 0);
    },
    successReport () {
      this.$bus.$emit('completeAdminWork', this.workid, 1);
    },
  }
}
</script>

<style scoped>
.card {
  border-radius: 10px;
  border: 1px solid red;
  text-align: left;
  font-size: 20px;
}
.right {
  float: right;
}
.item {
  margin-top: 10px;
}
.clickable {
  cursor: pointer;
}
.button {
  margin-top: 30px;
  text-align: center;
}
</style>